import React, { PureComponent, PropTypes } from 'react';
import classNames from 'classnames/bind';
import { TweenMax } from 'gsap';

import styles from './styles/Signature.scss';
const cx = classNames.bind(styles);

const canvas_border_width = 16 * 2;
/**
 * 1.
 * it will auto get position of contract signature area by id,
 * to set transformOrigin and scale 0 for zoom out with right position
 * 
 * 2.create canvas area by contract signature area proportion
 * 
 * @class Signature
 * @extends {PureComponent}
 */
class Signature extends PureComponent {
  constructor() {
    super()
    this.state = {
      transformOrigin: false,
      canvas: {
        height: false,
        width: false,
      }
    }
    this.isDrawing = false;
    this.lastPoint = false;
    this.ctx = false;
    this.rectSize = 60;
    this.alreadySign = false;
  }

  componentWillMount() {
    this._getScaleOrigin();
  }


  componentDidMount() {
    this._creatCanvas();
    window.addEventListener("orientationchange", (e) => {
      this._cleanSignature();
      setTimeout(this._creatCanvas, 0);
    });
  }

  _creatCanvas = () => {
    const {area_proportion} = this.props;
    const window_proportion = window.innerWidth / window.innerHeight;

    if (window_proportion > 1) { //landscape
      if (window_proportion > area_proportion) { // use max height
        console.log("land max height");
        this.setState({
          canvas: {
            width: window.innerHeight * area_proportion,
            height: window.innerHeight,
          }
        })
      } else { // use max width
        console.log("land max width");
        this.setState({
          canvas: {
            width: window.innerWidth,
            height: window.innerWidth / area_proportion,
          }
        })
      }
    } else { // portrait
      if (1 / window_proportion > area_proportion) { // use max width
        console.log("port max width");
        this.setState({
          canvas: {
            width: window.innerWidth,
            height: window.innerWidth * area_proportion,
          }
        })
      } else { // use max height
        console.log("port max height");
        console.log(window.innerHeight);
        this.setState({
          canvas: {
            width: window.innerHeight / area_proportion,
            height: window.innerHeight,
          }
        })
      }
    }

    const el = this.refCanvasSignature;
    this.ctx = el.getContext('2d');
    this.ctx.lineJoin = this.ctx.lineCap = 'round';
  }

  _drawStartSignature = (e) => {
    e.preventDefault();
    this.isDrawing = true;
    this.lastPoint = {
      x: e.touches[0].clientX - this.refCanvasSignature.offsetLeft,
      y: e.touches[0].clientY - this.refCanvasSignature.offsetTop
    };

  }

  _drawMoveSignature = (e) => {
    if (!this.isDrawing) return;
    var currentPoint = {
      x: e.touches[0].clientX - this.refCanvasSignature.offsetLeft,
      y: e.touches[0].clientY - this.refCanvasSignature.offsetTop
    };
    var dist = distanceBetween(this.lastPoint, currentPoint);
    var angle = angleBetween(this.lastPoint, currentPoint);
    var force = e.touches[0].force;
    for (var i = 0; i < dist; i++) {
      var x = this.lastPoint.x + (Math.sin(angle) * i);
      var y = this.lastPoint.y + (Math.cos(angle) * i);
      var radgrad = this.ctx.createRadialGradient(x, y, 0, x, y, this.rectSize / 3 * (force + 0.05));
      radgrad.addColorStop(0, '#000');
      radgrad.addColorStop(0.3, 'rgba(0,0,0,0.5)');
      radgrad.addColorStop(1, 'rgba(0,0,0,0)');
      this.ctx.fillStyle = radgrad;
      this.ctx.fillRect(x - this.rectSize / 2, y - this.rectSize / 2, this.rectSize, this.rectSize);
    }
    this.lastPoint = currentPoint;
  }

  _drawEndSignature = () => {
    this.isDrawing = false;
    this.alreadySign = true;
  }

  _getScaleOrigin = () => {
    var signature_area = document.getElementById(this.props.dom_signature_id);
    this.setState({
      transformOrigin: `${signature_area.offsetLeft + signature_area.clientWidth/2}px ${signature_area.offsetTop + signature_area.clientHeight/2}px`
    });
  }

  _cleanSignature = () => {
    const el = this.refCanvasSignature;
    this.ctx.clearRect(0, 0, el.width, el.height);
    this.alreadySign = false;
  }

  _completeSignature = () => {
    const {closeSignature} = this.props;
    let signature = this.refCanvasSignature.toDataURL();
    if (window.innerWidth / window.innerHeight < 1) {
      var tempImage = new Image();
      tempImage.onload = () => {
        const {width, height} = tempImage;
        const canvas_transform = document.createElement('canvas');
        const ctx = canvas_transform.getContext("2d");
        canvas_transform.width = height;
        canvas_transform.height = width;
        ctx.transform(0, -1, 1, 0, 0, width); // -90deg clock-wise
        ctx.drawImage(tempImage, 0, 0)
        signature = canvas_transform.toDataURL();
        closeSignature(signature, this.alreadySign);
      }
      tempImage.src = signature;
    } else {
      closeSignature(signature, this.alreadySign);
    }
  }

  render() {
    const {transformOrigin, canvas} = this.state;
    const {signature_open, closeSignature, area_proportion} = this.props;
    return (
      <div className={ styles.container + classNames({
                   " active": signature_open
                 }) } style={ { transformOrigin: transformOrigin } }>
        { area_proportion
          &&
          <canvas
            ref={ canvas => this.refCanvasSignature = canvas }
            width={ canvas.width }
            height={ canvas.height - canvas_border_width }
            onTouchStart={ this._drawStartSignature }
            onTouchMove={ this._drawMoveSignature }
            onTouchEnd={ this._drawEndSignature }></canvas> }
        <div className={ styles.box__btns }>
          <div className={ styles.btn__signatureClean } onClick={ this._cleanSignature }>
            <i className="fa fa-eraser" aria-hidden="true"></i>
          </div>
          <div className={ styles.btn__signatureDone } onClick={ this._completeSignature }>
            <i className="fa fa-check" aria-hidden="true"></i>
          </div>
        </div>
      </div>
      );
  }
}

function distanceBetween(point1, point2) {
  return Math.sqrt(Math.pow(point2.x - point1.x, 2) + Math.pow(point2.y - point1.y, 2));
}
function angleBetween(point1, point2) {
  return Math.atan2(point2.x - point1.x, point2.y - point1.y);
}

Signature.propTypes = {
  dom_signature_id: PropTypes.string.isRequired,
  signature_open: PropTypes.bool.isRequired,
  closeSignature: PropTypes.func.isRequired,
  area_proportion: PropTypes.number,
};

export default Signature ;



