import { Power2, TimelineLite } from 'gsap';

export default function animeSentOutContract(Dom_contract) {
  console.log('sent out');
  console.log(Dom_contract);
  console.log(Dom_contract.clientHeight);
  const tl = new TimelineLite();
  tl.to(Dom_contract, 0.3, {
    y: -Dom_contract.clientHeight,
    ease: Power2.easeOut
  })
}



